export default "import { useFlag } from '@yobgob/too-many-hooks'\nimport React from 'react'\nimport { Button, Modal as Modal_ } from '../../common/components'\nimport '../../index.css'\n\nconst Modal: React.FC = () => {\n  const [isOpen, { flag: open, unflag: close }] = useFlag(false)\n\n  return (\n    <>\n      <Button onClick={open} title=\"Open\" data-testid=\"open-button\">\n        Open modal\n      </Button>\n      <Modal_ isOpen={isOpen} onClose={close}>\n        <div className=\"prose leading-8\" data-testid=\"modal-text\">\n          This modal was opened with a simple<code>onClick=&#123;open&#125;</code>. Clicking\n          <code>&#10005;</code> or outside of the modal will close it via\n          <code>onClick=&#123;close&#125;</code>. No setters needed.\n        </div>\n      </Modal_>\n    </>\n  )\n}\n\nexport default Modal\n"